@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}
.visually-hidden-focusable:not(:focus):not(:focus-within) {
  clip: rect(0, 0, 0, 0) !important;
  border: 0 !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

body {
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  letter-spacing: 0.2px;
}

p {
  color: #737373;
  font-weight: 500;
  line-height: 1.42;
}

small {
  font-size: 12px;
  color: #BDBDBD;
  line-height: 2.3;
}

img {
  max-width: 100%;
}

.container {
  max-width: 1134px;
  margin: 0 auto;
  padding: 0 42px;
}

.btn {
  padding: 10px 36px;
  color: #FFFFFF;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 2;
  letter-spacing: 0.2px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  border: none;
}
.btn:hover {
  opacity: 0.8;
}
.btn.btn-primary {
  background-color: #FFA62B;
  border-radius: 5px;
  padding: 15px 40px;
}
.btn.btn-secondary {
  background-color: #00A0C1;
  border-radius: 37px;
}
.btn.btn-ghost {
  background-color: transparent;
  border-radius: 37px;
  border: 1px solid #00A0C1;
  color: #00A0C1;
}
.btn:disabled {
  background-color: #BDBDBD;
  cursor: not-allowed;
}
.btn.loading {
  position: relative;
}
.btn.loading:before {
  content: "";
  position: absolute;
  z-index: 5;
  top: 30%;
  right: 20px;
  width: 24px;
  height: 24px;
  border: 4px solid #FFFFFF;
  border-bottom-color: #FFA62B;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  background-color: transparent;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
label {
  display: block;
}
label span {
  display: inline-block;
  margin-bottom: 10px;
  color: #252B42;
  font-weight: 600;
  line-height: 1.7;
  cursor: pointer;
}

input[type=text],
input[type=email],
select,
textarea {
  border-radius: 5px;
  border: 1px solid #BDBDBD;
  background: #F9F9F9;
  padding: 10px 20px;
  color: #737373;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  width: 100%;
  line-height: 2;
  margin-bottom: 7px;
  height: 50px;
}

textarea {
  height: 140px;
}

.error-msg {
  color: red;
}

.success-msg {
  color: #14ae5c;
}

.form-msg {
  text-align: center;
  margin-top: 16px;
}

.section-title {
  text-align: center;
  max-width: 470px;
  margin: 0 auto;
}

.section-desc {
  text-align: center;
  margin: 10px auto 0;
  max-width: 470px;
}

.link {
  color: #FFA62B;
  font-weight: 600;
  line-height: 1.7;
  text-decoration: none;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  line-height: 1.42;
  color: #252B42;
}

h1, .h1 {
  font-size: 58px;
}

h2, .h2 {
  font-size: 40px;
}

h3, .h3 {
  font-size: 24px;
}

h4, .h4 {
  font-size: 20px;
}

h5, .h5 {
  font-size: 16px;
}

h6, .h6 {
  font-size: 14px;
}

@media (max-width: 1023px) {
  h1, .h1 {
    font-size: 40px;
  }
}
