.section-video {
  padding: 80px 0;
  background-color: #FAFAFA;
  position: relative;
}
.section-video:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background-color: #16697A;
  z-index: 0;
}
.section-video:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
